@import "hljs-atom-one-light.css";
@import "hljs-atom-one-dark.css";

/* Font Size Scaling */
:root {
    font-size: 16px;
}

@media (min-width: 2560px) {
    :root {
        font-size: 20px;
    }
}

/* SVG Stroke Color */
:root {
    --stroke-color: #334155;

    .icon {
        fill: var(--stroke-color);
    }
}

@media (prefers-color-scheme: dark) {
    :root {
        --stroke-color: #d1d5db;

        .icon {
            fill: var(--stroke-color);
        }
    }
}

/* HLJS */

.hljs {
    display: block;
    margin: 16px 0;
    padding: 8px 16px;
    border-radius: 4px;
    overflow-x: scroll;
}