/* HF Marked */

@layer utilities {
  .markdown {
    h1 {
      @apply mt-2 mb-8 text-4xl;
    }

    h2 {
      @apply mt-8 mb-2 text-3xl;
    }

    h3 {
      @apply mt-4 mb-2 text-2xl;
    }

    h4, h5, h6 {
      @apply mt-4 mb-2;
    }

    p {
      @apply my-8;
    }

    mjx-container:not([display="true"]) {
      @apply inline-block align-middle px-1 py-0.5;
    }

    mjx-container[display="true"] {
      @apply block mx-auto overflow-x-scroll;

      & > svg {
        @apply block mx-auto;
      }
    }

    figure {
      @apply mx-auto my-8 w-fit h-fit;

      img {
        @apply w-full h-full min-h-[360px] max-h-[400px] min-w-[240px] max-w-[480px] object-cover rounded-lg bg-8;
      }

      figcaption {
        @apply text-center text-sm text-8 pt-3;
      }
    }

    ul {
      @apply list-disc;
    }

    ol {
      @apply list-decimal;
    }

    ul, ol {
      li {
        @apply mb-2;
      }

      ul, ol {
        @apply my-4 ml-8;
      }
    }

    b, .bold, strong, .strong {
      @apply font-bold text-primary-32;
    }

    em, .em {
      @apply px-1 text-primary-32;
    }
  }

  .toc {
    & > * {
      @apply leading-8 truncate;

      &[aria-level="1"] {
        @apply pl-0;
      }


      &[aria-level="2"] {
        @apply pl-2;
      }


      &[aria-level="3"] {
        @apply pl-4;
      }


      &[aria-level="4"] {
        @apply pl-6;
      }


      &[aria-level="5"] {
        @apply pl-8;
      }


      &[aria-level="6"] {
        @apply pl-10;
      }
    }
  }

  blockquote {
    @apply border-l-[6px] border-stone-600 dark:border-stone-400 text-4 px-8 py-4 my-8;

    p:first-child {
      @apply mt-0;
    }

    p:last-child {
      @apply mb-0;
    }
  }

  blockquote[class^="blockquote-"] {
    @apply text-0 bg-amber-50 dark:bg-amber-950;

    .blockquote-title {
      @apply font-bold text-lg mb-4;
    }

    &.blockquote-quote {
      @apply bg-stone-50 dark:bg-stone-950;
    }

    &.blockquote-note {
      @apply border-lime-600 dark:border-lime-400 bg-lime-50 dark:bg-lime-950;
    }

    &.blockquote-tips {
      @apply border-teal-600 dark:border-teal-400 bg-teal-50 dark:bg-teal-950;
    }

    &.blockquote-info {
      @apply border-blue-600 dark:border-blue-400 bg-blue-50 dark:bg-blue-950;
    }

    &.blockquote-warning {
      @apply border-orange-600 dark:border-orange-400 bg-orange-50 dark:bg-orange-950;
    }

    &.blockquote-caution {
      @apply border-red-600 dark:border-red-400 bg-red-50 dark:bg-red-950;
    }

    &.blockquote-definition {
      @apply border-green-600 dark:border-green-400 bg-green-50 dark:bg-green-950;
    }

    &.blockquote-theorem {
      @apply border-purple-600 dark:border-purple-400 bg-purple-50 dark:bg-purple-950;
    }

    &.blockquote-corollary {
      @apply border-sky-600 dark:border-sky-400 bg-sky-50 dark:bg-sky-950;
    }

    &.blockquote-prof {
      @apply border-rose-600 dark:border-rose-400 bg-rose-50 dark:bg-rose-950;
    }
  }

  .prev-next {
    @apply flex justify-between mt-8;

    a {
      @apply block text-primary-32;

      &.prev::before {
        @apply inline-block pr-2;

        content: '←';
      }

      &.next::after {
        @apply inline-block pl-2;

        content: '→';
      }
    }
  }
}