@layer utilities {

  form {
    &.form-sm {
      @apply sm:mx-auto sm:w-full sm:max-w-sm;
    }

    h1 {
      @apply mb-4 heading-1 text-center;
    }

    .labeled-input {
      @apply py-4;

      &.expanded {
        @apply fixed z-50 top-0 left-0 h-screen w-screen px-16 py-8 bg-8 bg-opacity-50 overflow-hidden;

        textarea {
          @apply h-full max-h-max;
        }
      }

      .label-group {
        @apply flex items-center justify-between;

        .label {
          @apply block mb-2 font-medium text-sm leading-6;
        }

        .tips {
          @apply text-gray-500 text-sm font-bold;
        }

        .action {
          @apply text-primary-32 text-sm font-bold;
        }
      }
    }

    .input {
      @apply w-full px-4 py-2 rounded-md shadow-sm sm:text-sm sm:leading-6 bg-4 outline-none border-0 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-orange-400;

      &:-webkit-autofill,
      &:-webkit-autofill:hover,
      &:-webkit-autofill:focus {
        -webkit-text-fill-color: #000;
        // .shadow-sm | .ring-1.ring-inset.ring-gray-300 | styling removing for autofill.
        -webkit-box-shadow: 0 1px 2px 0 rgb(0 0 0 / 0.05), inset 0 0 0 calc(1px + 0px) rgb(209 213 219 / 1), 0 0 0 999px #fff inset;
      }

      @media (prefers-color-scheme: dark) {
        &:-webkit-autofill,
        &:-webkit-autofill:hover,
        &:-webkit-autofill:focus {
          -webkit-text-fill-color: #fff;
          -webkit-box-shadow: 0 0 0 40rem rgb(31, 41, 55) inset;
        }
      }
    }

    textarea {
      @apply w-full min-h-[480px] max-h-[640px] px-4 py-2 rounded-md shadow-sm sm:text-sm sm:leading-6 bg-4 outline-none whitespace-pre-wrap border-0 ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-orange-400;
    }

    .select {
      @apply block w-full px-4 py-2 rounded-md shadow-sm sm:text-sm sm:leading-6 bg-4 outline-none border-0 ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-orange-400;
    }

    .select-options {
      @apply overflow-y-scroll h-0 box-content duration-100 w-full mt-1 rounded-md bg-4 sm:text-sm shadow-xl;

      &.show {
        @apply max-h-[360px] h-fit;
      }

      & > * {
        @apply hover:bg-8 py-2 px-4;
      }
    }

    .stepper {
      @apply flex items-center justify-between w-full px-4 py-2 rounded-md shadow-sm sm:text-sm sm:leading-6 bg-4 outline-none border-0 ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-orange-400;

      button {
        @apply px-3 py-1;
      }
    }

    .grouped-input {
      @apply flex space-x-2;

      & > * {
        @apply flex-1;
      }
    }

    .form-tips {
      @apply mt-12 text-center text-gray-500 text-sm;

      a {
        @apply text-primary-32 font-bold px-1;
      }
    }

    .separator {
      @apply flex my-4 items-center;

      .left {
        @apply flex-1 h-0.5 bg-8;
      }

      .center {
        @apply px-4 text-gray-500 font-bold text-sm;
      }

      .right {
        @apply flex-1 h-0.5 bg-8;
      }
    }

    .actions {
      @apply w-fit flex content-around mx-auto my-4 space-x-4;

      button {
        @apply flex-1 w-48;
      }
    }
  }

  input[type="checkbox"] {
    @apply hidden;

    & + label {
      @apply w-4 h-4 rounded bg-0 border border-gray-700 dark:border-gray-300;
    }

    &:checked + label .icon {
      @apply opacity-100;
    }

    & + label {
      @apply block mx-auto relative;

      .icon {
        @apply opacity-0 w-3 h-3 absolute top-0 left-0 right-0 bottom-0 m-auto;
      }
    }
  }
}