@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  h1 {
    @apply heading-1;
  }

  h2 {
    @apply heading-2;
  }

  h3 {
    @apply heading-3;
  }

  h4 {
    @apply heading-4;
  }
}
@layer utilities {
  // MARK: Icons
  .icon {
    @apply inline w-6 h-6 align-middle;
  }

  // MARK: Brand
  .brand {
    .name {
      @apply h-8 font-bold text-xl leading-8;
    }
  }

  // MARK: Header
  .header {
    @apply flex px-16 py-8;

    &.header-sm {
      @apply py-4;
    }

    &.header-divider {
      @apply border-b border-gray-300 dark:border-gray-700;
    }

    .brand {
      @apply mx-auto block lg:flex lg:px-8;
    }

    .navbar {
      @apply flex-1 h-8 leading-8 justify-between hidden md:flex;
    }
  }

  // MARK: Footer
  .footer {
    @apply px-8 py-4 lg:flex lg:px-16 lg:py-8;

    .brand {
      @apply mx-auto block w-fit my-4 lg:flex lg:px-8 lg:my-auto;
    }

    .info {
      @apply text-center lg:text-left;
    }
  }

  // MARK: Dashboard
  .dashboard-content {
    @apply flex-1 overflow-x-hidden overflow-y-scroll px-8;
  }

  .dashboard-sidebar {
    @apply w-64 px-4 py-8 bg-0 h-full overflow-y-scroll border-r border-gray-300 dark:border-gray-700;

    ul {
      @apply pl-2 list-none overflow-hidden max-h-96 duration-500;

      &.collapsed {
        @apply max-h-0;
      }
    }

    nav > ul {
      @apply pl-0;
    }

    .label {
      @apply py-2 px-4 hover:bg-8 rounded text-4 font-medium flex;

      .name {
        @apply flex-1 leading-6;
      }

      .icon {
        @apply py-1 box-content;
      }
    }
  }

  .dashboard-table {
    @apply w-full rounded-xl bg-8;

    .table-prefix {
      @apply flex px-8 py-4 font-bold;

      .prefix {
        @apply flex-1;
      }

      .actions {
        @apply flex;
      }
    }

    .table-container {
      @apply max-w-full overflow-x-scroll;

      .table-head {
        @apply border-b py-2 pr-4;

        & > * {
          @apply inline-block;
        }
      }

      .table-body {
        @apply py-2;
      }

      .table-row {
        @apply block hover:bg-16 focus:bg-16 select-none pr-4;

        .row-content {
          @apply h-12 leading-12;

          & > * {
            @apply truncate inline-block align-middle ;
          }
        }

        .row-actions {
          @apply flex flex-row-reverse max-h-0 overflow-hidden;

          &.show {
            @apply max-h-16 pb-2;
          }

          & > * {
            @apply inline-block ml-2;
          }
        }
      }
    }
  }

  // MARK: Modal
  .modal {
    @apply left-0 right-0 top-0 bottom-0 fixed z-50 items-center justify-center bg-black bg-opacity-75;

    .container {
      @apply relative h-full w-full max-w-full;

      .modal-close {
        @apply absolute top-0 right-0 mx-8 my-4;

        .icon {
          @apply h-6 w-6 cursor-pointer;
        }
      }

      .modal-content {
        @apply absolute flex flex-col justify-between w-full max-w-[640px] h-full max-h-[720px] overflow-y-scroll bg-0 rounded-xl shadow-xl p-8;

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .modal-header {
          @apply flex justify-between align-middle leading-8;
        }
      }
    }
  }

  // MARK: Breadcrumbs
  .breadcrumbs {
    @apply text-8 overflow-x-scroll;

    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .bg-0 {
    @apply bg-white dark:bg-gray-900;
  }

  .bg-4 {
    @apply bg-gray-100 dark:bg-gray-800;
  }

  .bg-8 {
    @apply bg-gray-200 dark:bg-gray-700;
  }

  .bg-16 {
    @apply bg-gray-400 dark:bg-gray-600;
  }

  .bg-primary-0 {
    @apply bg-white dark:bg-gray-800;
  }

  .bg-primary-4 {
    @apply bg-orange-100 dark:bg-orange-800;
  }

  .bg-primary-8 {
    @apply bg-orange-200 dark:bg-orange-700;
  }

  .bg-primary-16 {
    @apply bg-orange-400 dark:bg-orange-600;
  }

  .text-0 {
    @apply text-gray-950 dark:text-gray-50;
  }

  .text-4 {
    @apply text-gray-700 dark:text-gray-300;
  }

  .text-8 {
    @apply text-gray-500 dark:text-gray-400;
  }

  .text-primary-0 {
    @apply text-orange-50 dark:text-orange-950;
  }

  .text-primary-4 {
    @apply text-orange-100 dark:text-orange-800;
  }

  .text-primary-8 {
    @apply text-orange-200 dark:text-orange-700;
  }

  .text-primary-16 {
    @apply text-orange-400 dark:text-orange-600;
  }

  .text-primary-32 {
    @apply text-orange-800 dark:text-orange-100;
  }

  .border-primary-0 {
    @apply border-orange-50 dark:border-orange-950;
  }

  .border-primary-4 {
    @apply border-orange-100 dark:border-orange-800;
  }

  .border-primary-8 {
    @apply border-orange-200 dark:border-orange-700;
  }

  .border-primary-16 {
    @apply border-orange-400 dark:border-orange-600;
  }

  .border-primary-32 {
    @apply border-orange-800 dark:border-orange-100;
  }

  :root, body, #root {
    @apply text-0 bg-0 min-h-screen w-full m-0;
  }

  main {
    @apply bg-4 w-full my-8 lg:my-0 lg:min-h-[640px] lg:py-16;
  }

  .container {
    @apply lg:flex lg:max-w-[1280px] mx-auto;

    .content {
      @apply lg:max-w-[960px] w-full mx-auto px-8 py-4 lg:px-16 lg:py-12 flex-1 bg-0 rounded-xl;
    }

    .sidebar {
      @apply lg:w-[300px] lg:min-h-[640px] lg:mx-16 px-8 py-12 bg-0 h-fit rounded-xl;
    }

    .sidebar + .content {
      @apply mx-16;
    }
  }

  .heading-1 {
    @apply text-0 font-bold text-2xl mb-4 tracking-tight;
  }

  .heading-2 {
    @apply text-0 font-bold text-xl mb-4 tracking-tight;
  }

  .heading-3 {
    @apply text-0 font-bold text-lg mb-2;
  }

  .heading-4 {
    @apply text-0 font-bold text-sm mb-2;
  }

  /* Table */
  table {
    @apply w-full;

    thead {
      @apply border-b border-gray-300;
    }

    td {
      @apply py-2 text-8 text-left whitespace-pre leading-4;

      &.primary {
        @apply text-0;
      }

      a {
        @apply text-primary-32 font-bold;
      }
    }

    th {
      @apply py-2 text-left;

      &:first-child {
        @apply pl-2;
      }

      &:last-child {
        @apply pr-2;
      }
    }

    &.row-prefixed {
      td:first-child {
        @apply font-bold text-0;
      }
    }
  }

  button, .button {
    @apply px-4 py-2 leading-6 border border-gray-700 dark:border-gray-300 rounded text-0 font-bold;
  }
}

@import "hfmarked";
@import "form";
@import "brand";
